.text-center {
  text-align: center;
}

.bg-primario {
  background-color: #e8505b;
}

/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Nunito", sans-serif;
  font-size: 1.6rem;
}

p {
  color: #000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  display: block;
}

h1, h2, h3 {
  margin: 0 0 5rem 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3.2rem;
}

.header {
  padding: 2rem 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .header {
    flex-direction: row;
    justify-content: space-between;
  }
}
.header__logo {
  width: 120px;
  height: 120px;
}
.header__image {
  border-radius: 50%;
}
.header__navegacion {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2rem;
  margin-top: 2rem;
}
@media (min-width: 768px) {
  .header__navegacion {
    margin-top: 0;
    flex-direction: row;
  }
}
.header__enlace {
  font-weight: 900;
  font-size: 2.5rem;
  color: #2B2F36;
}
.header__enlace:hover {
  font-weight: 900;
  font-size: 2.5rem;
  color: #FFD261;
}

.hero {
  min-height: 60rem;
  background-image: url(../img/hero1.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  color: #FFFFFF;
}
.hero__texto {
  max-width: 60rem;
}

.info__grid {
  display: grid;
  margin-top: 5rem;
  grid-template-columns: 1fr 1fr;
  gap: 4rem 4rem;
  padding-top: 2rem;
}
.info__container {
  min-width: 20rem;
  background-size: cover;
  background-position: center;
}
.info__container__image {
  border-radius: 2rem;
}
.info__texto {
  text-align: center;
  align-items: center;
  justify-content: center;
}
.info__title {
  font-size: 5rem;
}
.info__content {
  font-size: 3rem;
}

.join {
  margin-top: 5rem;
  min-height: 60rem;
  background-image: url(../img/profesores.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  color: #FFFFFF;
}
.join__texto {
  max-width: 60rem;
}

.advance__grid {
  margin-top: 5rem;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 4rem 4rem;
  padding-top: 2rem;
}
.advance__container {
  min-width: 20rem;
  background-size: cover;
  background-position: center;
}
.advance__container__image {
  border-radius: 2rem;
}
.advance__texto {
  text-align: center;
  align-items: center;
  justify-content: center;
}
.advance__title {
  font-size: 5rem;
}
.advance__content {
  font-size: 3rem;
}

.galery {
  margin-top: 5rem;
}
.galery__title {
  text-align: center;
}
.galery__grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, 1fr);
}
.galery__image1 {
  height: 40rem;
  background-image: url(../img/galeria1.png);
  background-repeat: no-repeat;
  background-size: cover;
  border: 1px solid black;
}
.galery__image2 {
  height: 40rem;
  background-image: url(../img/galeria2.png);
  background-repeat: no-repeat;
  background-size: cover;
  border: 1px solid black;
}
.galery__image3 {
  height: 40rem;
  background-image: url(../img/galeria3.png);
  background-repeat: no-repeat;
  background-size: cover;
  border: 1px solid black;
}
.galery__image4 {
  height: 40rem;
  background-image: url(../img/galeria4.png);
  background-repeat: no-repeat;
  background-size: cover;
  border: 1px solid black;
}

.footer {
  margin-top: 5rem;
  margin-bottom: 0;
  background-color: #222222;
  padding: 1rem 0;
}
.footer__contacto {
  margin-top: 4rem;
}
@media (min-width: 768px) {
  .footer__contacto {
    margin-top: 0rem;
  }
}
.footer__contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 2rem auto;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
}
@media (min-width: 768px) {
  .footer__contenedor {
    flex-direction: row;
    justify-content: space-between;
  }
}
.footer__navegacion {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2rem;
  margin-top: 2rem;
}
@media (min-width: 768px) {
  .footer__navegacion {
    margin-top: 0;
    flex-direction: row;
  }
}
.footer__enlace {
  font-weight: 900;
  font-size: 2.5rem;
}
.footer__text {
  color: #E1E1E1;
  font-size: 1.5rem;
}
.footer__text:hover {
  color: #FFD261;
}
.footer__raya {
  color: #E1E1E1;
}
.footer__derechos {
  color: white;
  font-size: 1.5rem;
  text-align: center;
}
.footer__redes {
  display: flex;
  flex-direction: row;
  gap: 3rem;
}